.nav-title {
    font-size: 24px;
    font-weight: 700;
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
    color: #f2f2f2;
    padding-left: 10px;
}

.nav {
  overflow: hidden;
  background-color: red;

}

.nav-link {
  float: left;
  color: #f2f2f2;
  background-color: red;
  text-align: center;
  padding: 24px 10px;
  text-decoration: none;
  font-size: 15px;

  /* these last two make the logout button in
  the nav bar look like another link */
  border:none;
  cursor: pointer;
  outline:0;
}

.nav-link:hover {
  background-color: red;
}

.nav-right {
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  width:100%;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

.sticky + .content {
  padding-top: 60px;
}
.content {
  padding: 16px;
}
footer {
    margin-top: 80px;
}

.headline{
    margin:10;
